.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container{
    max-width: 1140px;
    margin: auto;
    display: flex;
    flex-direction: column;
    column-gap: 40px;
    padding-bottom: 5rem;
}

.project-card{
     background: #3a2626;
     padding: 1.2rem 1rem;
}

.project-card img{
    width: 100%;
}

.project-title{
    color: antiquewhite;
    padding: 1rem 0;
}

.pro-btns{
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
}

.pro-btns .btn{
    padding: 0.5rem 1rem;
}
    

.project-details p{
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
}

@media screen and (max-width: 740px) {
    .project-container{
        max-width: 90%;
        margin: auto;

        grid-template-columns: 1fr;
    }
}