.bgImage{
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    position: relative;
    height: 60vh;
}

.bgImage::before{
    content: "";
    background: url("https://images.unsplash.com/photo-1587560699334-cc4ff634909a?q=80&w=870&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left:0 ;
    z-index: -1;
}

.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.bgImage h1{
    font-size: 2.4rem;
}

.bgImage p{
    font-size: 2.4rem;
    text-align: center;
}

@media screen and (max-width: 640px){
    .bgImage h1{
        font-size: 2rem;
    }
}